
import { Vue, Options } from 'vue-class-component';
@Options({
  name: "Notification"
})
export default class Notification extends Vue {
  openMessage() {
    (this as any).$notification({ message: "" });
  }
  openMessageSuccess() {
    (this as any).$notification({message: "", type: "success", center: false, timer: 3000}); 
  }
  openMessageerror() {
    (this as any).$notification({message: "", type: "error"}); 
  }
  openMessagewarn() {
    (this as any).$notification({message: "", type: "warning"}); 
  }
}
